* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Karla", sans-serif;
}

/* ==== Start ==== */
.app-title {
  color: #293264;
  font-size: 31.25px;
  font-weight: 700;
  margin: 8px;
}

.app-description {
  color: #293264;
  line-height: 19px;
  font-family: "Inter";
}

.startButton {
  font-family: "Inter";
  background-color: #4d5b9e;
  width: 193px;
  height: 52px;
  color: #f5f7fb;
  font-size: 16px;
  border: none;
  border-radius: 15px;
  font-weight: 500;
  margin: 29px;
}
.startButton:hover {
  cursor: pointer;
}
.startButton:active {
  background-color: #454d79;
}

/* ==== Quiz ==== */
.quizzes-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.loading-text {
  color: #293264;
}

.button-container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin-block: 10px;
}

.button {
  align-self: center;
  font-family: "Inter";
  font-weight: 600;
  font-size: 0.9rem;
  color: #f5f7fb;
  padding: 15px;
  background-color: #4d5b9e;
  border: none;
  border-radius: 10px;
}
.button:hover {
  cursor: pointer;
}
.button:active {
  background-color: #454d79;
}

/* ==== QuizItem ==== */
.quiz-item-container {
  border-bottom: 1px solid #dbdef0;
  margin-block: 14px;
}

.quiz-question {
  color: #293264;
  line-height: 19px;
  margin-bottom: 12px;
}

.option-container {
  display: flex;
}

.option {
  padding: 4px 15px;
  margin-right: 15px;
  margin-bottom: 15px;
  font-family: "Inter";
  color: #293264;
  font-size: 10.5px;
  font-weight: 500;
  display: flex;
  align-items: center;
  text-align: center;
  border: 2px solid #293264;
  border-radius: 8px;
}
.option.selected {
  background-color: #dbdef0;
}
.option:hover {
  cursor: pointer;
}

.option.answerChecked {
  background: none;
  opacity: 0.5;
}
.option.answerChecked.correct {
  opacity: 1;
  background-color: #94d7a2;
  border: none;
}
.option.answerChecked.wrong {
  background-color: #f8bcbc;
  border: none;
}
